<template>
  <footer class="fix-footer-button">
    <div class="fix-footer-select">
      <div class="fix-radio-select">
        <span class="fix-select-text">球镜</span>
        <fix-foot-radio v-model="column" label="-">-</fix-foot-radio>
        <fix-foot-radio style="margin: 0 12px" v-model="column" label="+"
          >+</fix-foot-radio
        >
      </div>
      <div class="fix-radio-select">
        <span class="fix-select-text">柱镜</span>
        <fix-foot-radio v-model="header" label="-">-</fix-foot-radio>
        <fix-foot-radio style="margin: 0 12px" v-model="header" label="+"
          >+</fix-foot-radio
        >
      </div>
    </div>
    <div class="fix-foot-content">
      <button class="fix-edit-button" @click="edit">修改数量</button>
      <button class="fix-cart-button" @click="add">加入购物车</button>
    </div>
  </footer>
</template>

<script>
import FixFootRadio from '../fix-foot-radio'

export default {
  name: 'fix-foot-button',
  props: {
    columnType: {
      type: Boolean,
      default: true
    },
    headerType: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      column: '+',
      header: '+'
    }
  },
  methods: {
    edit() {
      this.$emit('edit')
    },
    add() {
      this.$emit('add')
    },
    change() {
      this.$emit('change', this.header === '+', this.column === '+')
    }
  },
  watch: {
    header() {
      this.change()
    },
    column() {
      this.change()
    },
    columnType: {
      handler() {
        if (this.columnType) {
          this.column = '+'
        } else {
          this.column = '-'
        }
      },
      immediate: true
    },
    headerType: {
      handler() {
        if (this.headerType) {
          this.header = '+'
        } else {
          this.header = '-'
        }
      },
      immediate: true
    }
  },
  components: {
    [FixFootRadio.name]: FixFootRadio
  }
}

</script>

