<template>
  <transition name="toast-fade">
    <div class="bo-toast" v-if="value" v-text="text"></div>
  </transition>
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    text: {
      type: String,
      default: ''
    }
  },
  name: 'bo-toast',
  watch: {
    value() {
      if (this.value) {
        this.timer && clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.$emit('input', false)
        }, 3000)
      }
    }
  }
}

</script>

<style scoped lang="stylus">
.bo-toast {
  position: fixed;
  top: 50%;
  left: 50%;
  padding: 9px 12px;
  line-height: 1;
  font-size: 13px;
  color: #fff;
  z-index: 1000;
  transform: translate3d(-50%, -50%, 0);
  background-color: rgba(51, 51, 51, 0.8);
  user-select: none;
  border-radius: 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.toast-fade-enter-active, .toast-fade-leave-active {
  transition: opacity 0.3s ease-out;
}

.toast-fade-enter, .toast-fade-leave-to {
  opacity: 0;
}
</style>
