// import axios from './axios.js'
import axios from './mock.js'

export default {
  data() {
    return {
      brandName: '', // 品牌名
      seriesName: '', // 系列名
      goodName: '' // 商品名称
    }
  },
  created() {
    this.goodsId = 'E52AAE59-8149-11EB-B6C5-00163E06CF73' // 商品id
    this.brandName = '依视路' // 品牌名
    this.seriesName = '视满分' // 系列名
    this.goodName = '球面 1.591 白晶 PC片' // 商品名称
    this.init()
  },
  methods: {
    async init() {
      // 初始化函数
      this.dataJson = {} // key-value 对象，快速匹配商品
      this.dataJsonType = [] // 记录接口类型是否加载
      this.loading = true
      await this.getLensSpecList(4)
      this.loading = false
      this.getCaceData()
      this.objectToData(this.dataJson)
      this.typeChange(this.headerType, this.columnType)
    },
    getLensSpecList(type) {
      // 请求列表接口
      const url = '/index.php?s=/index/test/Lens_spec_list'
      return axios.post(url, { goods_id: this.goodsId, type }).then(res => {
        const data = res.data.specList
        data.forEach((item, $index) => {
          item.cylList.forEach((d, $$index) => {
            this.dataJson[`${d.sph},${d.cyl}`] = {
              ...d,
              goodsIdKey: `goodlist[${$index}][goods_id]`,
              goodsBaseIdKey: `goodlist[${$index}][spec][${$$index}][goods_base_id]`,
              stockKey: `goodlist[${$index}][spec][${$$index}][stock]`,
              priceKey: `goodlist[${$index}][spec][${$$index}][price]`
            }
          })
        })
        this.dataJsonType.push(type)
      })
    },
    confirmOrder(goodlist) {
      // 添加购物车
      const url = '/index.php?s=/index/test/cart_save'
      axios.post(url, {
        order_form: 3,
        ...goodlist
      }).then(res => {
        // 成功清空内容
        if (res.code === 0) {
          this.data = []
          this.getCaceDataInit()
          localStorage.removeItem('bathOrderForBj')
          this.showToast('加入购物车成功')
        } else if (res.code === -100) {
          this.showToast('您选择的商品有变动，请重新选择下单')
          window.location.reload()
          // this.loadingAllData()
        }
      })
    },
    async loadingAllData() {
      this.loading = true
      await this.getLensSpecList(1)
      await this.getLensSpecList(2)
      await this.getLensSpecList(3)
      await this.getLensSpecList(4)
      this.loading = false
    },
    includesType(h, c) {
      if (!h && !c) return this.dataJsonType.includes(1)
      if (h && !c) return this.dataJsonType.includes(2)
      if (!h && c) return this.dataJsonType.includes(3)
      return this.dataJsonType.includes(4)
    }
  }
}
