<template>
  <transition name="slide-up">
    <div class="number-keypad" v-if="value">
      <div class="number-keypad-content">
        <div class="number-keypad-item" v-for="n in num" :key="n" @click="change(n)">{{ n }}</div>
        <div
          class="number-keypad-item number-keypad-action"
          @click="closePopup"
        >
          <img src="./images/arrow.png" class="number-keypad-arrow" />
        </div>
        <div class="number-keypad-item" @click="change(0)">0</div>
        <div class="number-keypad-item number-keypad-action" @click="remove">
          <img src="./images/delete.png" class="number-keypad-delete" />
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  name: 'number-keypad',
  data() {
    return {
      num: [1, 2, 3, 4, 5, 6, 7, 8, 9]
    }
  },
  methods: {
    change(n) {
      this.$emit('change', n)
    },
    remove() {
      this.$emit('remove')
    },
    closePopup() {
      this.$emit('input', false)
    }
  }
}

</script>

