  position: fixed;
  left: 0;
  bottom: 0;
  padding-bottom: 34px;
  width: 100%;
  z-index: 1000;
  transform: translate3d(0, 0,0);
  background-color: #f8f8f8;
  user-select: none;
}

.number-keypad-content {
  margin-right: -1px;
  background-color: #fff;
  font-size: 0;
  overflow: hidden;
}

.number-keypad-item {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 33.3%;
  height: 46px;
  line-height: 46px;
  font-size: 14px;
  color: #333;
  text-align: center;
  border-right: 1px solid #f8f8f8;
  border-top: 1px solid #f8f8f8;
  background-color: #fff;

  &:active {
    background-color: #f3f3f3;
  }
}

.number-keypad-delete, .number-keypad-arrow {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.number-keypad-action {
  background-color: #f8f8f8;
}

.number-keypad-delete {
  width: 36px;
  height: 27px;
}

.number-keypad-arrow {
  width: 23px;
  height: 23px;
}

.slide-up-enter-active, .slide-up-leave-active{
  transition: transform .3s ease-out;
}
.slide-up-enter, .slide-up-leave-to {
  transform: translate3d(0, 100%,0);
}
</style>
