<template>
  <div class="excel-table-header">
    <table style="table-layout: fixed">
      <thead>
        <tr>
          <th class="excel-table-th excel-table-th-fix-left"
            :style="`width:${cellWidth}px;`"
          >
            <div class="excel-border-th excel-table-box-arg" :style="`width:${cellWidth}px`">
              <span class="excel-table-title right">柱镜</span>
              <span class="excel-table-title">球镜</span>
              <div class="excel-table-slash"></div>
            </div>
          </th>
          <td
            class="excel-table-td"
            v-for="(header, $index) in headers"
            :class="{
              [`excel-table-hover`]: xIndex.includes($index)
            }"
            :style="`width:${cellWidth}px;`"
            @click="selectCell($index)"
            :key="$index"
          >
            <div class="excel-border-th"  :style="`width:${cellWidth}px;`">{{ header }}</div>
          </td>
        </tr>
      </thead>
    </table>
  </div>
</template>

<script>
export default {
  name: 'excel-table-header',
  inject: ['state'],
  computed: {
    headers() {
      return this.state.headers
    },
    columns() {
      return this.state.columns
    },
    xIndex() {
      return this.state.xIndex
    },
    cellWidth() {
      return this.state.cellWidth
    },
    cellHeight() {
      return this.state.cellHeight
    }
  },
  methods: {
    selectCell(x) {
      this.$emit('selectCell', x, -1, 'vertical')
    }
  }
}

</script>

<style scoped lang="stylus">
.excel-table-header {
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  z-index: 2;
  height: 34px;
  z-index: 30;
  background-color: #f8f8f8;
  .excel-table-th,
  .excel-table-td{
    background-color: #f8f8f8;
  }
}

</style>
