/* eslint-disable */
const specList = []
let sph = 44
for (let i = 0; i < 44; i++) {
  const cylList = []
  sph = i - 0.25
  let cyl = 44
  for (let j = 0; j < 44; j++) {
    cyl = j - 0.25
    cylList.push({
      sph: '+' + sph,
      cyl: '+' + cyl,
      disabled: false,
      num: 0,
      price: "5.00"
    })
  }
  specList.push({
    sph: '+' + sph,
    cylList
  })
}
const d = {
  msg: "获取sku列表成功!",
  "code": 0,
  "data": {
    specList
  }
}

const axios = {
  post(url, data) {
    return new Promise(function (resolve, reject) {
      resolve(d)
    })
  }
}

export default axios
