import Vue from 'vue'
import App from './App.vue'

const baseSize = 10
function setRem() {
  const scale = document.documentElement.clientWidth / 375
  document.documentElement.style.fontSize = (baseSize * Math.min(scale, 2)) + 'px'
}
setRem()
window.onresize = setRem

new Vue({
  render: h => h(App)
}).$mount('#app')
