<template>
  <div class="excel-table-body" :style="tableStyle" ref="table">
    <table style="table-layout: fixed">
      <thead>
        <tr v-for="(column, $index) in columns" :key="$index">
          <td
            class="excel-table-td excel-column-th excel-table-th-fix-left"
            :class="{
              [`excel-table-hover`]: yIndex.includes($index),
            }"
            @click="selectCell(-1, $index, 'horizontal')"
            :style="`width:${cellWidth}px;height:${cellHeight}px`"
          >
            <div class="excel-border-td">{{ column }}</div>
          </td>
          <td
            class="excel-table-td excel-table-cell"
            :class="{
              [`excel-table-hover`]:
                xIndex.includes($$index) || yIndex.includes($index),
              [`excel-table-disabled`]: disabled.includes(
                $$index + headers.length * $index
              ),
              [`excel-table-y${$index}`]: true,
              [`excel-table-x${$$index}`]: true,
            }"
            v-for="(header, $$index) in headers"
            :key="$$index"
            @click="radioCell($$index, $index, 'cell')"
            :style="`width:${cellWidth}px;height:${cellHeight}px`"
            ref="cell"
          >
            <div :style="`width:${cellWidth}px;height:${cellHeight}px`" class="excel-border-td">{{ data[$$index + headers.length * $index] }}</div>
          </td>
        </tr>
      </thead>
    </table>
    <excel-table-grid
      :gridCells="gridCells"
      @selectAuxiliaryLine="selectAuxiliaryLine"
      ref="grid"
    ></excel-table-grid>
  </div>
</template>

<script>
import ExcelTableGrid from './excel-table-grid'

export default {
  name: 'excel-table-column',
  inject: ['state', 'uid'],
  props: {
    height: {
      type: Number,
      default: 320
    }
  },
  data() {
    return {
      gridCells: []
    }
  },
  computed: {
    headers() {
      return this.state.headers
    },
    columns() {
      return this.state.columns
    },
    disabled() {
      return this.state.disabled
    },
    data() {
      return this.state.data
    },
    xIndex() {
      return this.state.xIndex
    },
    yIndex() {
      return this.state.yIndex
    },
    tableStyle() {
      return `max-height: ${this.height}px;`
    },
    cellWidth() {
      return this.state.cellWidth
    },
    cellHeight() {
      return this.state.cellHeight
    }
  },
  mounted() {
    this.getCellWidthOrHeight()
    const resize = () => {
      this.getCellWidthOrHeight()
      this.$nextTick(() => {
        this.$refs.grid.selectGrid(this.$refs.grid.type)
      })
    }
    this.$once('hook:beforeDestroy', function () {
      window.removeEventListener('resize', resize)
    })
  },
  methods: {
    getCellWidthOrHeight() {
      this.$nextTick(() => {
        const table = this.$refs.table
        let cells = table.querySelectorAll('.excel-table-cell')
        const data = []
        for (let i = 0; i < cells.length; i++) {
          const cell = cells[i]
          const { x, y } = this.getCellsIndex(cell)
          data.push({
            x,
            y,
            minX: this.cellWidth * x + this.cellWidth,
            minY: this.cellHeight * y,
            width: this.cellWidth,
            height: this.cellHeight,
            maxX: this.cellWidth * x + 2 * this.cellWidth,
            maxY: this.cellHeight * y + this.cellHeight
          })
        }
        this.gridCells = data
      })
    },
    radioCell(x, y, type) {
      this.$emit('radioAuxiliaryLine', x, y)
      this.$nextTick(() => {
        this.$refs.grid.selectGrid(type)
      })
    },
    selectCell(x, y, type) {
      const xs = []
      const ys = []
      for (let i = 0; i < this.gridCells.length; i++) {
        if (this.gridCells[i].y === y) {
          xs.push(this.gridCells[i].x)
          ys.push(y)
        }
        if (this.gridCells[i].x === x) {
          xs.push(x)
          ys.push(this.gridCells[i].y)
        }
      }
      this.$emit('selectAuxiliaryLine', xs, ys)
      this.$nextTick(() => {
        this.$refs.grid.selectGrid(type)
      })
    },
    selectAuxiliaryLine(x, y) {
      this.$emit('selectAuxiliaryLine', x, y)
    },
    getCellsIndex(e) {
      const classList = e.className.split(' ')
      const d = {}
      classList.forEach(c => {
        if (c.indexOf('excel-table-x') > -1) {
          d.x = Number(c.replace('excel-table-x', ''))
        }
        if (c.indexOf('excel-table-y') > -1) {
          d.y = Number(c.replace('excel-table-y', ''))
        }
      })
      return d
    }
  },
  components: {
    [ExcelTableGrid.name]: ExcelTableGrid
  }
}

</script>

<style scoped lang="stylus">
.excel-table-body {
  position: relative;
}

.excel-column-th {
  background-color: #f8f8f8;
}

.excel-table-disabled {
  position: relative;
  background-color: #e5e5e5 !important;
  overflow: hidden;

  &::before {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      to top right,
      transparent 0%,
      transparent calc(50% - 1px),
      #ccc 50%,
      transparent calc(50% + 1px),
      transparent 100%
    );
    content: ' ';
  }
}
</style>
