<template>
  <div class="fix-foot-radio" @click="radioChange">
    <span
      class="fix-foot-input"
      :class="{ 'is-checked': value === label }"
    ></span>
    <span class="fix-foot-label"><slot></slot></span>
  </div>
</template>

<script>
export default {
  name: 'fix-foot-radio',
  props: ['value', 'label'],
  data() {
    return {}
  },
  methods: {
    radioChange() {
      this.$emit('input', this.label)
    }
  }
}

</script>

<style scoped lang="stylus">
.fix-foot-radio {
  display: flex;
  align-items: center;
  padding: 4px 0;
  height: 24px;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;

  .fix-foot-label {
    margin-left: 4px;
    height: 24px;
    line-height: 24px;
    font-size: 18px;
    font-weight: 500;
    color: #707070;
  }

  .fix-foot-input {
    position: relative;
    display: block;
    width: 16px;
    height: 16px;
    border: 1px solid #f5b03e;
    border-radius: 50%;
    overflow: hidden;

    &.is-checked::before {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 8px;
      height: 8px;
      background-color: #f5b03e;
      border-radius: 50%;
      overflow: hidden;
      transform: translate(-50%, -50%);
      content: ' ';
    }
  }
}
</style>
