<template>
  <div class="all-order-page">
    <transition name="fade">
      <div class="loading-wrapper" v-if="loading">
        <base-loading></base-loading>
      </div>
    </transition>
    <excel-table
      :headers="headers"
      :columns="columns"
      :data="data"
      :disabled="disabled"
      :height="height"
      ref="table"
    ></excel-table>
    <fix-foot-button
      :headerType="headerType"
      :columnType="columnType"
      @change="typeChange"
      @edit="edit"
      @add="add"
    ></fix-foot-button>
    <number-keypad
      v-model="numberPopup"
      @change="numberChange"
      @remove="numberRemove"
    ></number-keypad>
    <bo-toast v-model="toastPopup" :text="toastMessage"></bo-toast>
    <detail-order
      :brandName="brandName"
      :seriesName="seriesName"
      :goodName="goodName"
      @confirm="confirm"
      :detailData="detailData"
      v-model="detailPopup"
    ></detail-order>
  </div>
</template>

<script>
import ExcelTable from './components/excel-table'
import FixFootButton from './components/fix-foot-button'
import NumberKeypad from './components/number-keypad'
import DetailOrder from './components/detail-order'
import BOToast from './components/bo-toast'
import cannotMixin from './mixins/cannot'
import BaseLoading from './components/base-loading'

export default {
  name: 'batch-order',
  mixins: [cannotMixin],
  data() {
    return {
      height: 0,
      headerType: true,
      columnType: true,
      headers: [],
      columns: [],
      data: [],
      detailData: [],
      disabled: [],
      toastPopup: false,
      loading: false,
      toastMessage: '',
      numberPopup: false, // 数字键盘弹窗
      detailPopup: false // 批量明显单弹窗
    }
  },
  mounted() {
    document.title = '云镜台批量下单'
    const body = window.innerHeight
    const footer = document.querySelector('.fix-footer-button')
    this.height = body - footer.clientHeight - 54
  },
  methods: {
    edit() {
      const xIndexs = this.$refs.table.getXIndex()
      const yIndexs = this.$refs.table.getYIndex()
      if (!xIndexs.length && !yIndexs.length) return this.showToast('请选择光度')
      this.numberPopup = true
      this.numberPopupAction = true
    },
    add() {
      const key = this.getCaceKey(this.headerType, this.columnType)
      this.cace[key] = this.data.slice()
      const detailData = []
      let replaceObject = {}
      for (const x in this.cace) {
        const data = this.cace[x]
        const uiData = this.getUiData(!(x.indexOf('zhuNegative') > -1), !(x.indexOf('qiuNegative') > -1))
        const headers = uiData.headers
        const columns = uiData.columns
        for (const index in data) {
          if (data[index]) {
            const num = Number(data[index])
            const headerIndex = index % headers.length
            const columnIndex = Math.floor(index / headers.length)
            const zhu = headers[headerIndex]
            const qiu = columns[columnIndex]
            const name = `${qiu},${zhu}`
            if (replaceObject[name] !== undefined) {
              const d = replaceObject[name]
              detailData[d].num += num
            } else {
              // 0.00,0.00 可能不存在，但是UI需要展示
              if (this.dataJson[name]) {
                replaceObject[name] = detailData.length
                detailData.push({
                  zhu,
                  qiu,
                  num,
                  price: this.dataJson[name].price
                })
              }
            }
          }
        }
      }
      if (!detailData.length) return this.showToast('请选择光度并输入需要购买的数量')
      this.detailPopup = true
      this.detailData = detailData.sort((a, b) => {
        if (a.zhu === '0.00' && a.qiu === '0.00') return -1
        if (b.zhu === '0.00' && b.qiu === '0.00') return 1
        const qiuValA = this.stringToNumber(a.qiu)
        const zhuValA = this.stringToNumber(a.zhu)
        const qiuValB = this.stringToNumber(b.qiu)
        const zhuValB = this.stringToNumber(b.zhu)
        const qiuDec = Math.abs(qiuValA) - Math.abs(qiuValB)
        const zhuDec = Math.abs(zhuValA) - Math.abs(zhuValB)
        // 同象限方向
        if (qiuValA / qiuValB > 0) {
          if (qiuValA === qiuValB) {
            if (zhuValA / zhuValB > 0) {
              return zhuDec
            } else if (zhuValA === 0) {
              return -1
            } else if (zhuValB === 0) {
              return 1
            }
            return zhuValA - zhuValB
          }
          return qiuDec
        } else if (qiuValA === 0) {
          return -1
        } else if (qiuValB === 0) {
          return 1
        } else {
          return qiuValA - qiuValB
        }
      })
      this.detailData = detailData.map(item => ({
        zhu: item.zhu,
        qiu: item.qiu,
        num: item.num,
        price: item.price
      }))
    },
    async typeChange(h, c) {
      // h true柱镜+ false柱镜-
      // c true球镜+ false球镜-
      if (this.loading) return false
      if (!this.includesType(h, c)) {
        this.loading = true
        let _type = 4
        if (!h && !c) _type = 1
        if (h && !c) _type = 2
        if (!h && c) _type = 3
        await this.getLensSpecList(_type)
        this.objectToData(this.dataJson)
      }
      const key = this.getCaceKey(h, c)
      const caceKey = this.getCaceKey(this.headerType, this.columnType)
      const caceData = this.data.slice()
      this.data = this.cace[key] || []
      this.cace[caceKey] = caceData
      this.headerType = h
      this.columnType = c
      const d = this.getUiData(h, c)
      this.headers = d.headers
      this.columns = d.columns
      this.disabled = d.disabled
      this.disabled.forEach(item => {
        this.data[item] = null
      })
      if (this.$refs.table) {
        this.$refs.table.setXIndex([])
        this.$refs.table.setYIndex([])
      }
      this.loading = false
    },
    numberChange(n) {
      const data = this.data.slice()
      const len = this.headers.length
      const selectCellId = this.$refs.table.getSelectCellId()
      let resetNumber = this.selectCellId !== selectCellId
      if (this.numberPopupAction) {
        resetNumber = true
        this.numberPopupAction = false
      }
      this.numberAction((x, y) => {
        const index = x + len * y
        const val = data[index] || ''
        if (!val && n === 0) return false
        if (this.disabled.includes(index)) return false
        let num = String(n)
        if (resetNumber) {
          if (n === 0) {
            data[index] = ''
          } else {
            data[index] = num
          }
        } else {
          if (String(val).length >= 5) {
            num = ''
          }
          data[index] = `${val}${num}`
        }
      })
      this.data = data
      this.timeSaveData()
      this.selectCellId = selectCellId
    },
    numberRemove() {
      const data = this.data.slice()
      const len = this.headers.length
      this.numberAction((x, y) => {
        const index = x + len * y
        const val = data[index] || ''
        if (this.disabled.includes(index)) return false
        data[index] = val.slice(0, val.length - 1)
      })
      this.data = data
      this.timeSaveData()
    },
    numberAction(cb) {
      const xIndexs = this.$refs.table.getXIndex()
      const yIndexs = this.$refs.table.getYIndex()
      xIndexs.forEach(x => {
        yIndexs.forEach(y => {
          cb(x, y)
        })
      })
      this.xIndexs = xIndexs
      this.yIndexs = yIndexs
    },
    showToast(text) {
      this.toastPopup = true
      this.toastMessage = text
    },
    objectToData(data) {
      const zhu = []
      const zhuNegative = []
      const qiu = []
      const qiuNegative = []
      for (const key in data) {
        const name = key.split(',')
        const qiuNum = this.stringToNumber(name[0])
        const zhuNum = this.stringToNumber(name[1])
        this.numberClassified(name[0], qiuNum, qiu, qiuNegative)
        this.numberClassified(name[1], zhuNum, zhu, zhuNegative)
      }
      this.zhu = this.dataSort(zhu)
      this.zhuNegative = this.dataSort(zhuNegative, true)
      this.qiu = this.dataSort(qiu)
      this.qiuNegative = this.dataSort(qiuNegative, true)
    },
    dataSort(d, f) {
      if (f) {
        return d.sort((a, b) => b - a)
      }
      return d.sort((a, b) => a - b)
    },
    numberClassified(name, num, o, n) {
      if (num >= 0 && !o.includes(name)) {
        o.push(name)
      } else if (num <= 0 && !n.includes(name)) {
        n.push(name)
      }
    },
    stringToNumber(n) {
      return Number(n.replace(/\+/g, ''))
    },
    getCaceKey(h, c) {
      return `${h ? 'zhu' : 'zhuNegative'}${c ? 'qiu' : 'qiuNegative'}`
    },
    getCaceDataInit() {
      this.cace = {
        [this.getCaceKey(true, true)]: [],
        [this.getCaceKey(true, false)]: [],
        [this.getCaceKey(false, true)]: [],
        [this.getCaceKey(false, false)]: []
      }
    },
    getCaceData() {
      this.getCaceDataInit()
      let data = localStorage.getItem('bathOrderForBj')
      try {
        if (data) {
          const d = JSON.parse(data)
          for (const x in this.cace) {
            this.cace[x] = d[x] || []
          }
        }
      } catch (error) {
      }
    },
    setCaceData() {
      const key = this.getCaceKey(this.headerType, this.columnType)
      this.cace[key] = this.data.slice()
      localStorage.setItem('bathOrderForBj', JSON.stringify(this.cace))
    },
    getUiData(h, c) {
      // h true柱镜+ false柱镜-
      // c true球镜+ false球镜-
      let headers = h ? this.zhu : this.zhuNegative
      let columns = c ? this.qiu : this.qiuNegative
      const disabled = []
      const headerDisabled = {}
      const columnDisabled = {}
      if (headers.length * columns.length > 0) {
        for (let i = 0; i < headers.length * columns.length; i++) {
          const headerIndex = i % headers.length
          const columnIndex = Math.floor(i / headers.length)
          const zhu = headers[headerIndex]
          const qiu = columns[columnIndex]
          const keyIndex = `${qiu},${zhu}`
          if (!this.dataJson[keyIndex] || (this.dataJson[keyIndex] && this.dataJson[keyIndex].disabled)) {
            if (!headerDisabled[headerIndex]) {
              headerDisabled[headerIndex] = { headerIndex, columnIndex, len: 0 }
            }
            if (!columnDisabled[columnIndex]) {
              columnDisabled[columnIndex] = { headerIndex, columnIndex, len: 0 }
            }
            headerDisabled[headerIndex].len += 1
            columnDisabled[columnIndex].len += 1
          }
        }
        const deleteHeaderLen = headers.length
        const deleteColumnLen = columns.length
        for (const x in headerDisabled) {
          if (headerDisabled[x].len >= deleteColumnLen) {
            // 需要剔除这列
            headers = headers.slice()
            headers.splice(headerDisabled[x].headerIndex, 1)
          }
        }
        for (const x in columnDisabled) {
          if (columnDisabled[x].len >= deleteHeaderLen) {
            // 需要剔除这行
            columns = columns.slice()
            columns.splice(columnDisabled[x].columnIndex, 1)
          }
        }
        for (let i = 0; i < headers.length * columns.length; i++) {
          const headerIndex = i % headers.length
          const columnIndex = Math.floor(i / headers.length)
          const zhu = headers[headerIndex]
          const qiu = columns[columnIndex]
          const keyIndex = `${qiu},${zhu}`
          if (!this.dataJson[keyIndex]) {
            disabled.push(i)
          }
          if (this.dataJson[keyIndex] && this.dataJson[keyIndex].disabled) {
            disabled.push(i)
          }
        }
      }
      return { headers, columns, disabled }
    },
    timeSaveData() {
      this.inputSetCaceTimer && clearTimeout(this.inputSetCaceTimer)
      this.inputSetCaceTimer = setTimeout(() => {
        this.setCaceData()
      }, 1000)
    },
    confirm(data) {
      const goodlist = {}
      data.forEach(item => {
        const d = this.dataJson[`${item.qiu},${item.zhu}`]
        goodlist[d.goodsIdKey] = this.goodsId
        goodlist[d.goodsBaseIdKey] = d.id
        goodlist[d.stockKey] = item.num
        goodlist[d.priceKey] = d.price
      })
      this.confirmOrder(goodlist)
    }
  },
  components: {
    [ExcelTable.name]: ExcelTable,
    [FixFootButton.name]: FixFootButton,
    [NumberKeypad.name]: NumberKeypad,
    [DetailOrder.name]: DetailOrder,
    [BOToast.name]: BOToast,
    [BaseLoading.name]: BaseLoading
  }
}
</script>

<style lang="stylus">
.all-order-page {
  padding: 10px 0 10px 14px;
}

.all-order-page * {
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
  -webkit-overflow-scrolling: auto;
}

.loading-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
