<template>
  <div class="detail-order">
    <transition name="fade">
      <div class="detail-order-mark" v-if="value"></div>
    </transition>
    <transition name="dialog-do-fade">
      <div class="detail-order-content" v-if="value">
        <h4 class="detail-order-title">批量明细单</h4>
        <div class="detail-close-wrapper" @click="hideDetailDialog">
          <img src="./images/close.png" class="detail-close" />
        </div>
        <div class="detail-order-body">
          <div class="order-tag">
            <span class="detail-order-tag tag1">{{ brandName }}</span>
            <span class="detail-order-tag tag2">{{ seriesName }}</span>
          </div>
          <h5 class="detail-order-subtitle">{{ goodName }}</h5>
          <div class="detail-order-scroll">
            <div
              class="detail-order-item"
              v-for="(detail, $index) in detailData"
              :key="$index"
            >
              <span class="detail-order-text"
                >球镜{{ detail.qiu }} 柱镜{{ detail.zhu }}</span
              >
              <span class="detail-order-price">¥{{ detail.price }}</span>
              <span class="detail-order-num">x{{ detail.num }}</span>
            </div>
          </div>
          <div class="detail-order-footer">
            <span class="detail-order-count"
              >共{{ detailData.length }}件商品 合计：<strong
                >￥{{ total }}</strong
              ></span
            >
          </div>
        </div>
        <span class="detail-order-button" @click="confirm">确认</span>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'detail-order',
  props: {
    value: {
      type: Boolean,
      default: false
    },
    detailData: {
      type: Array,
      default: () => []
    },
    brandName: {
      type: String,
      default: ''
    },
    seriesName: {
      type: String,
      default: ''
    },
    goodName: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      total: 0
    }
  },
  methods: {
    hideDetailDialog() {
      this.$emit('input', false)
    },
    confirm() {
      this.$emit('input', false)
      this.$emit('confirm', this.detailData)
    }
  },
  watch: {
    detailData() {
      let total = 0
      for (let i = 0; i < this.detailData.length; i++) {
        total += this.detailData[i].price * this.detailData[i].num
      }
      this.total = total.toFixed(2)
    }
  }
}

</script>

