<template>
  <div
    class="excel-table-grid"
    ref="wrapper"
    v-show="xIndex.length && yIndex.length"
  >
    <span>{{text}}</span>
    <div
      class="excel-drag"
      @touchstart="(e) => touchstart(e, 'x')"
      @touchmove="touchmove"
      @touchend="touchend"
      @touchcancel="touchend"
      ref="dragX"
    >
      <div class="excel-grid-cell"></div>
    </div>
    <div
      class="excel-drag"
      @touchstart="(e) => touchstart(e, 'y')"
      @touchmove="touchmove"
      @touchend="touchend"
      @touchcancel="touchend"
      ref="dragY"
    >
      <div class="excel-grid-cell"></div>
    </div>
    <div
      class="excel-grid-active"
      :style="`top:${tip.y}px;left:${tip.x}px`"
      v-if="toucheX || toucheY"
    ></div>
  </div>
</template>

<script>
const TYPES = ['cell', 'horizontal', 'vertical']

export default {
  name: 'excel-table-grid',
  inject: ['state', 'uid', 'top', 'left', 'right', 'bottom'],
  props: {
    gridCells: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      text: '',
      toucheX: false,
      toucheY: false,
      width: 0,
      height: 0,
      tip: { x: 0, y: 0 },
      type: 'cell'
    }
  },
  computed: {
    headers() {
      return this.state.headers
    },
    columns() {
      return this.state.columns
    },
    xIndex() {
      return this.state.xIndex
    },
    yIndex() {
      return this.state.yIndex
    },
    xl() {
      return this.state.headers.length
    },
    yl() {
      return this.state.columns.length
    }
  },
  mounted() {
    this.$nextTick(() => {
      const scroll = document.querySelector('.' + this.uid)
      const scroller = () => {
        this.setDrag()
      }
      scroll.addEventListener('scroll', scroller)
      this.$once('hook:beforeDestroy', () => {
        scroll.removeEventListener('scroll', scroller)
      })
      this.scroll = scroll
    })
  },
  methods: {
    selectGrid(type) {
      if (type === TYPES[1]) {
        // 横向选择
        this.type = TYPES[1]
      } else if (type === TYPES[2]) {
        // 纵向选择
        this.type = TYPES[2]
      } else {
        this.type = TYPES[0]
      }
      // 单元选择
      this.box = this.getCentent()
      this.setWrapper(this.box.minX, this.box.minY, this.box.maxX, this.box.maxY)
    },
    touchstart(e, t) {
      this.toucheX = t === 'x'
      this.toucheY = t === 'y'
      this.startX = e.touches[0].clientX
      this.startY = e.touches[0].clientY
      this.tip.x = e.touches[0].pageX
      this.tip.y = e.touches[0].pageY
      const topRect = this.getBoundingClientRect(this.top)
      const leftRect = this.getBoundingClientRect(this.left)
      const rightRect = this.getBoundingClientRect(this.right)
      const bottomRect = this.getBoundingClientRect(this.bottom)
      this.boxTopMax = topRect.top + topRect.height
      this.boxLeftMax = leftRect.left + leftRect.width
      this.boxRightMax = rightRect.left
      this.boxBottomMax = bottomRect.top
    },
    touchmove(e) {
      this.clearTimeout()
      e.preventDefault()
      e.stopPropagation()
      if (this.toucheX || this.toucheY) {
        const clientX = e.touches[0].clientX
        const clientY = e.touches[0].clientY
        this.tip.x = e.touches[0].pageX
        this.tip.y = e.touches[0].pageY
        this.offsetX = clientX - this.startX
        this.offsetY = clientY - this.startY
        this.computedRect(this.offsetX, this.offsetY)
        this.dragScroll(this.offsetX, this.offsetY, this.tip.x, this.tip.y)
      }
    },
    touchend(e, t) {
      this.clearTimeout()
      this.toucheX = false
      this.toucheY = false
      this.box = this.getCentent()
      this.setWrapper(this.box.minX, this.box.minY, this.box.maxX, this.box.maxY)
    },
    computedRect(offsetX, offsetY) {
      const box = { ...this.box }
      if (this.type === TYPES[1]) {
        // 横向选择
        offsetX = 0
      } else if (this.type === TYPES[2]) {
        offsetY = 0
      }
      if (this.toucheY) {
        box.maxX += offsetX
        box.maxY += offsetY
      } else {
        box.minX += offsetX
        box.minY += offsetY
      }
      if (box.minX > box.maxX) {
        if (offsetX > 0) {
          box.minX = this.box.minX
          box.maxX = this.box.maxX + offsetX - (this.box.maxX - this.box.minX)
        } else {
          box.minX = this.box.maxX + offsetX
          box.maxX = this.box.maxX
        }
      }
      if (box.minY > box.maxY) {
        if (offsetY > 0) {
          box.minY = this.box.minY
          box.maxY = this.box.maxY + offsetY - (this.box.maxY - this.box.minY)
        } else {
          box.minY = this.box.maxY + offsetY
          box.maxY = this.box.maxY
        }
      }
      const x = []
      const y = []
      for (let i = 0; i < this.gridCells.length; i++) {
        const item = this.gridCells[i]
        if (this.aabb(item, box)) {
          if (!x.includes(item.x)) x.push(item.x)
          if (!y.includes(item.y)) y.push(item.y)
        }
      }
      this.$emit('selectAuxiliaryLine', x, y)
      this.$nextTick(() => {
        const _box = this.getCentent()
        this.setWrapper(_box.minX, _box.minY, _box.maxX, _box.maxY)
      })
    },
    aabb(box1, box2) {
      var x1 = box1.minX >= box2.minX && box1.minX < box2.maxX
      var x2 = box2.minX >= box1.minX && box2.minX < box1.maxX
      var y1 = box1.minY >= box2.minY && box1.minY < box2.maxY
      var y2 = box2.minY >= box1.minY && box2.minY < box1.maxY
      return (x1 || x2) && (y1 || y2)
    },
    getCentent() {
      const minXIndex = this.xIndex[0]
      const maxXIndex = this.xIndex[this.xIndex.length - 1]
      const minYIndex = this.yIndex[0]
      const maxYIndex = this.yIndex[this.yIndex.length - 1]
      const box = {}
      for (let i = 0; i < this.gridCells.length; i++) {
        const item = this.gridCells[i]
        if (item.x === minXIndex && item.y === minYIndex) {
          box.minX = item.minX
          box.minY = item.minY
        }
        if (item.x === maxXIndex && item.y === maxYIndex) {
          box.maxX = item.maxX
          box.maxY = item.maxY
        }
      }
      return box
    },
    setWrapper(minX, minY, maxX, maxY, z) {
      const el = this.$refs.wrapper
      if (el) {
        const l = minX
        const t = minY
        const w = maxX - minX
        const h = maxY - minY
        this.width = w
        this.height = h
        el.style.cssText = `top:${t}px;left:${l}px;width:${w}px;height:${h}px;`
        if (z) {
          el.style.zIndex = z
        }
        this.text = `${l}-${w}`
        this.setDrag()
      }
    },
    setDrag() {
      const dragX = this.$refs.dragX
      const dragY = this.$refs.dragY
      if (this.type === TYPES[1]) {
        // 横向选择
        let x = 0
        if (this.width > this.scroll.clientWidth) {
          x = this.scroll.clientWidth / 2 + this.scroll.scrollLeft
        } else {
          x = this.width / 2
        }
        dragX.style.cssText = `left:${x}px;top:0;transform: translate(-50%, -50%);-webkit-transform:translate(-50%,-50%);`
        dragY.style.cssText = `left:${x}px;bottom:0;transform:translate(-50%, 50%);-webkit-transform:translate(-50%,50%);`
      } else if (this.type === TYPES[2]) {
        // 纵向选择
        let y = 0
        if (this.height > this.scroll.clientHeight) {
          y = this.scroll.clientHeight / 2 + this.scroll.scrollTop
        } else {
          y = this.height / 2
        }
        dragX.style.cssText = `top:${y}px;left:0;transform: translate(-50%, -50%);-webkit-transform:translate(-50%,-50%);`
        dragY.style.cssText = `top:${y}px;right:0;transform:translate(50%, -50%);-webkit-transform:translate(50%,-50%);`
      } else {
        // 单元选择
        dragX.style.cssText = `top:0;left:0;transform: translate(-50%, -50%);-webkit-transform:translate(-50%,-50%);`
        dragY.style.cssText = `bottom:0;right:0;transform:translate(50%, 50%);-webkit-transform:translate(50%,50%);`
      }
    },
    dragScroll(offsetX, offsetY, pageX, pageY) {
      let x = 0
      let y = 0
      let speed = 5
      if (this.boxLeftMax >= pageX) {
        x -= speed
      } else if (this.boxRightMax <= pageX) {
        x = speed
      }
      if (this.boxTopMax >= pageY) {
        y -= speed
      } else if (this.boxBottomMax <= pageY) {
        y = speed
      }
      if (x !== 0 || y !== 0) {
        this.autoOffset(x, y, offsetX, offsetY, this.scroll)
      }
    },
    autoOffset(x, y, offsetX, offsetY, el) {
      this.clearTimeout()
      this.autoOffsetTimer = setTimeout(() => {
        const _offsetX = offsetX + x
        const _offsetY = offsetY + y
        el.scrollLeft += x
        el.scrollTop += y
        if (x > 0 && el.scrollWidth - el.scrollLeft !== el.clientWidth) {
          this.startX -= x
        } else if (x < 0 && el.scrollLeft > 0) {
          this.startX -= x
        }
        if (y > 0 && el.scrollHeight - el.scrollTop !== el.clientHeight) {
          this.startY -= y
        } else if (y < 0 && el.scrollTop > 0) {
          this.startY -= y
        }
        this.computedRect(_offsetX, _offsetY)
        this.autoOffset(x, y, _offsetX, _offsetY, el)
      }, 17)
    },
    clearTimeout() {
      if (this.autoOffsetTimer) {
        clearTimeout(this.autoOffsetTimer)
        this.autoOffsetTimer = null
      }
    },
    getBoundingClientRect(className) {
      return document.querySelector('.' + className).getBoundingClientRect()
    }
  }
}

</script>

<style scoped lang="stylus">
.excel-table-grid {
  position: absolute;
  background-color: rgba(210, 210, 210, 0.32);
  border: 2PX solid #FFAE00;
  pointer-events: none;
  z-index: 10;
}

.excel-drag {
  position: absolute;
  width: 24px;
  height: 24px;
  pointer-events: auto;
}

.excel-grid-cell {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.excel-grid-cell {
  width: 6px;
  height: 6px;
  border: 1PX solid #FFAE00;
  background-color: #fff;
  box-sizing: content-box;
  z-index: 10;
}

.excel-grid-active {
  position: fixed;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  background-color: rgba(249, 232, 192, 0.4);
  overflow: hidden;
  transform: translate(-21px, -21px);
  z-index: 200;
}
</style>
