<template>
  <div class="excel-table" :style="tableStyle">
    <div class="excel-table-line-width" :style="`${tableStyle};height:1px;`"></div>
    <div class="excel-table-line-height" :style="`${tableStyle};width:1px;`"></div>
    <div class="excel-table-content" :class="[uid]">
      <excel-table-header
        @selectCell="selectCell"
        @radioAuxiliaryLine="radioAuxiliaryLine"
      ></excel-table-header>
      <excel-table-column
        :height="height"
        @radioAuxiliaryLine="radioAuxiliaryLine"
        @selectAuxiliaryLine="selectAuxiliaryLine"
        ref="column"
      ></excel-table-column>
      <div class="excel-table-box" :class="[top]" :style="topBox"></div>
      <div class="excel-table-box" :class="[left]" :style="leftBox"></div>
      <div class="excel-table-box" :class="[right]" :style="rightBox"></div>
      <div class="excel-table-box" :class="[bottom]" :style="bottomBox"></div>
    </div>
  </div>
</template>

<script>
import ExcelTableHeader from './excel-table-header'
import ExcelTableColumn from './excel-table-column'

export default {
  name: 'excel-table',
  provide() {
    return {
      state: this.state,
      uid: this.uid,
      top: this.top,
      left: this.left,
      right: this.right,
      bottom: this.bottom
    }
  },
  props: {
    headers: {
      type: Array,
      default: () => []
    },
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Array,
      default: () => []
    },
    height: {
      type: Number,
      default: 320
    }
  },
  data() {
    return {
      uid: this.guid(),
      top: this.guid(),
      left: this.guid(),
      right: this.guid(),
      bottom: this.guid(),
      selectCellId: this.guid(),
      state: {
        cellWidth: 52,
        cellHeight: 28,
        cellThHeight: 35,
        xIndex: [], // 是否选中表头
        yIndex: [], // 是否选中列
        headers: [], // 表头
        columns: [], // 列
        disabled: [], // 禁止列
        data: [] // 表头和列的一维数组
      }
    }
  },
  computed: {
    topBox() {
      return `top: 0;left: 0;width: 100%;height: ${this.state.cellThHeight}px;`
    },
    leftBox() {
      return `top: 0;left: 0;width: ${this.state.cellWidth}px;height: 100%;`
    },
    rightBox() {
      return `top: 0;right: 0;width: ${this.state.cellWidth}px;height: 100%;`
    },
    bottomBox() {
      return `bottom: 0;left: 0;width: 100%;height: ${this.state.cellHeight}px;`
    },
    tableStyle() {
      const width = this.state.headers.length * this.state.cellWidth + this.state.cellWidth
      const height = this.state.columns.length * this.state.cellHeight + this.state.cellThHeight
      let cssText = ''
      if (width < window.innerWidth) {
        cssText = `width:${width}px;`
      }
      if (height < window.innerHeight) {
        cssText += `height:${height}px;`
      }
      return cssText
    }
  },
  mounted() {
    const resize = () => {
      this.refreshTable()
    }
    this.$once('hook:beforeDestroy', function () {
      window.removeEventListener('resize', resize)
    })
    window.addEventListener('resize', resize)
    this.refreshTable()
  },
  methods: {
    refreshTable() {
      const cellWidth = document.createElement('div')
      const cellHeight = document.createElement('div')
      const cellThHeight = document.createElement('div')
      cellWidth.style.cssText = 'width:5.2rem;height:5.2rem'
      cellHeight.style.cssText = 'width:2.8rem;height:2.8rem'
      cellThHeight.style.cssText = 'width:3.4rem;height:3.4rem'
      document.body.appendChild(cellWidth)
      document.body.appendChild(cellHeight)
      document.body.appendChild(cellThHeight)
      this.$nextTick(() => {
        this.state.cellWidth = Math.ceil(cellWidth.getBoundingClientRect().width)
        this.state.cellHeight = Math.ceil(cellHeight.getBoundingClientRect().width)
        this.state.cellThHeight = Math.ceil(cellThHeight.getBoundingClientRect().width)
        document.body.removeChild(cellWidth)
        document.body.removeChild(cellHeight)
        document.body.removeChild(cellThHeight)
      })
    },
    refresh() {
      this.timer && clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.$refs.column && this.$refs.column.getCellWidthOrHeight()
      }, 40)
    },
    radioAuxiliaryLine(x, y) {
      this.state.xIndex = [x]
      this.state.yIndex = [y]
      this.selectCellId = this.guid()
    },
    selectAuxiliaryLine(x, y) {
      this.state.xIndex = [...new Set(x.sort((a, b) => a - b))]
      this.state.yIndex = [...new Set(y.sort((a, b) => a - b))]
      this.selectCellId = this.guid()
    },
    selectCell(x, y, type) {
      this.$refs.column.selectCell(x, y, type)
    },
    getSelectCellId() {
      return this.selectCellId
    },
    getXIndex() {
      return this.state.xIndex
    },
    getYIndex() {
      return this.state.yIndex
    },
    setXIndex(val) {
      this.state.xIndex = val
    },
    setYIndex(val) {
      this.state.yIndex = val
    },
    guid() {
      return 'uxxxxxxxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        const r = Math.random() * 16 | 0
        const v = c === 'x' ? r : (r & 0x3 | 0x8)
        return v.toString(16)
      })
    }
  },
  watch: {
    headers: {
      handler() {
        this.state.headers = this.headers
      },
      immediate: true
    },
    columns: {
      handler() {
        this.state.columns = this.columns
        this.refresh()
      },
      immediate: true
    },
    data: {
      handler() {
        this.state.data = this.data
        this.refresh()
      },
      immediate: true
    },
    disabled: {
      handler() {
        this.state.disabled = this.disabled
        this.refresh()
      },
      immediate: true
    }
  },
  components: {
    [ExcelTableHeader.name]: ExcelTableHeader,
    [ExcelTableColumn.name]: ExcelTableColumn
  }
}

</script>

