    width: 100%;
    height: 100%;
    overflow: auto;

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    &::-webkit-scrollbar-thumb {
      border-radius: 0;
    }
    &::-webkit-scrollbar-track {
      box-shadow: none;
      background: transparent;
    }
  }

  & >>> table {
    width: 100%;
    text-align: left;
    border-collapse: separate;
    border-spacing: 0;
    box-sizing: border-box;
  }

  & >>> .excel-table-th {
    height: 34px;
    min-height: 34px;
    max-height: 34px;
    background-color: #f8f8f8;
    box-sizing: border-box;
  }

  & >>> .excel-table-td {
    height: 28px;
    min-height: 28px;
    max-height: 28px;
    box-sizing: border-box;
  }

  & >>> .excel-border-th {
    width: 100%;
    height: 34px;
    line-height: 34px;
    border-bottom: 1PX solid #ccc;
    border-right: 1PX solid #ccc;
    box-sizing: border-box;
  }

  & >>> .excel-border-td {
    width: 100%;
    height: 28px;
    line-height: 28px;
    border-bottom: 1PX solid #ccc;
    border-right: 1PX solid #ccc;
    box-sizing: border-box;
  }

  & >>> .excel-table-th, & >>> .excel-table-td {
    padding: 0;
    width: 52px;
    font-size: 12px;
    color: #333;
    text-align: center;
    box-sizing: border-box;
    text-overflow: ellipsis;
    vertical-align: middle;
    overflow: hidden;
  }

  & >>> .excel-table-th-fix-left {
    position: sticky;
    position: -webkit-sticky;
    left: 0;
    z-index: 20;
    overflow: hidden;
    background-color: #f8f8f8;
  }

  & >>> .excel-table-hover {
    background-color: #FFF9EB !important;
  }

  & >>> .excel-table-slash {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
