    background: linear-gradient(
      to top right,
      transparent 0%,
      transparent calc(50% - 1px),
      #ccc 50%,
      transparent calc(50% + 1px),
      transparent 100%
    );
  }

  & >>> .excel-table-title {
    display: block;
    padding: 0 2px;
    width: 100%;
    height: 17px;
    line-height: 17px;
    text-align: left;
